.loader-wrapper {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: grid;
    place-items: center;
}

.loader {
    border-left: 0.5px solid gainsboro;
    position: absolute;
    left: 50%;
    margin-left: -3px;
    height: 95%;
    animation: slideDown 2.4s ease-in-out forwards;
}

@keyframes slideDown {
    from {
        transform: translate(0, -100%);
    }
    to {
        transform: translate(0, 120%);;
    }
}

.pcols {
    width: 100%;
    height: 100%;
    position: fixed;
    bottom: 0;
    right: 0;
}

.blocks {
    float: left;
    width: 20%;
    height: 100%;
    background-color: #161616;
}

.blocksgroup1 {
    position: relative;
    animation: slideUpColumn 0.6s ease-in-out forwards;
    animation-delay: 1.4s;
}

.blocksgroup2 {
    position: relative;
    animation: slideDownColumn 0.7s ease-in-out forwards;
    animation-delay: 1.8s;
}
.blocksgroup22 {
    position: relative;
    animation: slideDownColumn 0.7s ease-in-out forwards;
    animation-delay: 2s;
}

.blocksgroup3 {
    position: relative;
    animation: slideUpColumn 0.8s ease-in-out forwards;
    animation-delay: 2.3s;
}
.blocksgroup33 {
    position: relative;
    animation: slideUpColumn 0.8s ease-in-out forwards;
    animation-delay: 2.1s;
}

@keyframes slideDownColumn {
    from{
        transform: translate(0,0%);
    }
    to{
        transform: translate(0, 100%);
    }
}

@keyframes slideUpColumn {
    from{
        transform: translate(0,0%);
    }
    to{
        transform: translate(0, -100%);
    }
}